import * as React from "react";
import { graphql, PageProps } from "gatsby";
import Img from "gatsby-image";
import Navbar from "../components/Navbar";
import Button from "../components/Button";
import Footer from "../components/Footer";
import { SEO } from "../components/SEO";

const NotFoundPage: React.FC<PageProps> = (props) => {
  const data = props.data as any;
  const backgroundImage = data.backgroundImage;
  return (
    <main>
      <div className="relative">
        <Img
          fluid={backgroundImage.childImageSharp.fluid}
          alt={backgroundImage.name}
          className="!absolute z-[-1] w-full h-full !object-cover !object-center"
        />
        <div className="blur-background">
          <Navbar />
        </div>
        <div className="mt-40 blur-background">
          <div className="py-16 container grid grid-cols-1 lg:grid-cols-2 items-end gap-20 lg:gap-0">
            <div>
              <span className="block text-5xl mb-3">404</span>
              <span className="block text-5xl mb-3">Page</span>
              <span className="block text-5xl mb-3">Not Found.</span>
              <div className="mb-10"></div>
              <Button to="/" primary>
                Go to home
              </Button>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </main>
  );
};

export default NotFoundPage;

export const Head = () => <SEO title={`404 | VPTronics`} />;

export const query = graphql`
  query {
    backgroundImage: file(relativePath: { eq: "solar-farm.jpg" }) {
      name
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
